body {
    &::-webkit-scrollbar {
        width: 12px;

        &-track {
            background-color: $background;
        }

        &-thumb {
            background-color: rgba(255, 255, 255, 0.75);
            border-radius: 50px;

            &:hover {
                background-color: rgba(255, 255, 255, 1);
            }
        }
    }
}

i, p, button, input, span {
    font-size: $font-size;
    font-style: normal !important;
    font-family: $first-font, $second-font;
}

.wrapper-outer {
    width: 100vw;
    min-height: 100vh;
    background-color: $background;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    padding-top: 70px;
    padding-bottom: 70px;
    box-sizing: border-box;
    
    @media (max-width: 480px) {
        padding: 30px 0 40px 0;
    }

    .wrapper {
        width: 550px;
        box-sizing: border-box;
        background-color: white;
        padding: 0 35px;
        border-radius: 10px;
        background-color: $background-lite;

        @media (max-width: 480px) {
            padding: 0 20px;
            width: 100vw;
        }
    }
}

.btn {
    background-color: white;
    color: $button-main;
    text-transform: uppercase;
    font-weight: 500;
    transition: .2s;
    cursor: pointer;
    border: none;

    &:hover {
        background-color: $button-main;
        color: white !important;
    }

    &:focus {
        background-color: $button-main;
        outline: none;
        color: white;
    }
}

.input {
    border: none;
    padding: 15px 25px;
    box-sizing: border-box;
    font-family: $first-font, $second-font;
    font-weight: 500;
    color: $text-main;
    font-size: $font-size + 0.05;

    &:focus {
        outline: none;
    }
}

.dummy {
    display: none;
}
